<template>
  <MyFrame>
    <el-card>
      <div slot="header">
        <span></span>
      </div>
      <el-row>
        <el-col span=6>
          <el-select value="" v-model="manufacture" placeholder="">
            <el-option value="" label=""></el-option>
            <el-option value="" label=""></el-option>
            <!--                <el-option value="" label=""></el-option>-->
            <!--                <el-option value="" label="1"></el-option>-->
            <!--                <el-option value="2" label="2"></el-option>-->
          </el-select>
        </el-col>
        <el-col span=10>
          <el-date-picker
            v-model="date"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-col>
        <el-col span=3 push=4>
          <el-button type="primary" v-on:click="searchClicked"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <el-row>
      <el-col span=9>
        <el-card>
          <div id="chart1" style="height: 300px;"></div>
        </el-card>

      </el-col>
      <el-col span=14 push=1>
        <el-card>
          <div id="chart2" style="height: 300px;"></div>
        </el-card>
      </el-col>
    </el-row>
    <el-row>
      <el-col span=9>
        <el-card>
          <div id="chart3" style="height: 300px;"></div>
        </el-card>

      </el-col>
      <el-col span=14 push=1>
        <el-card>
          <div id="chart4" style="height: 300px;"></div>
        </el-card>
      </el-col>
    </el-row>
  </MyFrame>
</template>

<script>
  import * as echarts from 'echarts';
  import Navmenu from "../components/NavMenu";
  import Sidebar from "../components/Sidebar"
  import MyFrame from "../components/Frame";

  export default {
    name: "usageSurvey",
    components: {MyFrame, Navmenu, Sidebar},
    data: function () {
      return {
        manufacture: '',
        date: ''
      }
    },
    mounted() {
      var chart1 = document.getElementById("chart1");
      var chart2 = document.getElementById("chart2");
      var chart3 = document.getElementById("chart3");
      var chart4 = document.getElementById("chart4");

      chart1 = echarts.init(chart1,'halloween');
      chart2 = echarts.init(chart2,'halloween');
      chart3 = echarts.init(chart3,'halloween');
      chart4 = echarts.init(chart4,'halloween');

      var option1 = {
        title: {
          text: '\n',
          // subtext: ''
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {
          data: ['', '']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value',
          // data:['','','','']
        },
        yAxis: {
          type: 'category',
          data: ['', '', '', '']
        },
        series: [
          {
            name: '',
            type: 'bar',
            data: [100, 96, 22, 45]
          },
          {
            name: '',
            type: 'bar',
            data: [65, 40, 31, 55]
          }
        ]
      };

      var option2 = {
        title: {
          text: ""
        },
        legend: {},
        tooltip: {},
        dataset: {
          source: [
            ['product', '', '', '', ''],
            ['', 43.3, 85.8, 93.7, 22.4],
            ['', 83.1, 73.4, 55.1, 11.2],
            ['', 86.4, 65.2, 82.5, 43.4],
            ['', 72.4, 53.9, 39.1, 30.3]
          ]
        },
        xAxis: {type: 'category'},
        yAxis: {},
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [
          {type: 'bar'},
          {type: 'bar'},
          {type: 'bar'},
          {type: 'bar'}
        ]
      };

      var option3 = {
        title: {
          text: '31',
          // subtext: '',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          data: ['', '', '', '']
        },
        series: [
          {
            // name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              {value: 335, name: ''},
              {value: 310, name: ''},
              {value: 234, name: ''},
              {value: 135, name: ''},
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };

      var option4 = {
        title: {
          text: "7\n"
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {            // 
            type: 'shadow'        // 'line' | 'shadow'
          }
        },
        legend: {
          data: ['', '', '', '']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: ['', '', '', '', '', '', '']
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            stack: '',
            data: [320, 332, 301, 334, 390, 330, 320]
          },
          {
            name: '',
            type: 'bar',
            stack: '',
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '',
            type: 'bar',
            stack: '',
            data: [220, 182, 191, 234, 290, 330, 310]
          },
          {
            name: '',
            type: 'bar',
            stack: '',
            data: [150, 232, 201, 154, 190, 330, 410]
          }
        ]
      };

      chart1.setOption(option1);
      chart2.setOption(option2);
      chart3.setOption(option3);
      chart4.setOption(option4)


    }
  }
</script>

<style scoped>
  .el-row {
    margin-bottom: 20px;
  }

  .el-card {
    margin-bottom: 20px;
  }
</style>



